.tmpNote {
  width: 100px;
  height: 75px;
  border: 1px solid darkkhaki;
  padding: var(--space-s);
  margin: var(--space-s);
  background-color: lightyellow;
  word-break: break-all;
  text-wrap: pretty;
}

.emptyNote {
  width: 100px;
  height: 75px;
  padding: calc(var(--space-s));
  margin: var(--space-s);
}

.slice {
  height: 0;
  width: 0;
  left: -100px;
  position: relative;
  font-family: Arial, Helvetica, sans-serif;
  color: var(--color-neutral-6);
  z-index: -1;
}

.slice-label {
  padding-top: 10px;
  width: 100px;
  overflow-wrap: break-word;
  font-size: small;
}

.slice::after {
  content: '';
  border-top: 2px solid lightgray;
  position: absolute;
  /* width: calc(100vw - 490px); */
  /* width: 100vw; */
  width: 100px;
  top: -2px;
}

.board {
  margin-left: 450px;
  padding-left: 100px;
  width: calc(100vw - 570px);
  height: 100vh;
  overflow: scroll;
}

.backbones {
  border-top: 2px solid lightgray;
}

.slices {
  border-top: 2px solid lightgray;
}